@import "vars";

.wwa {
  transition: width 1s;
  @media screen and (max-width: 1100px) {
    width: calc(100% - 57px);
  }
  @media screen and (max-width: 450px) {
    width: 95%;
  }
  position: relative;
  margin: 148px auto;
  max-width: 754px;
  &__title {
    margin-bottom: 16px;
    @include important-font;
    font-weight: 600;
    font-size: 32px;
    line-height: 150%;
    color: $c-black;
  }
  &__description {
    @include regular-font;
    color: #6c788b;
    font-size: 24px;
    line-height: 150%;

  }
}
